<template>
  <div>
<el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="活动名称" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="活动期限" :label-width="formLabelWidth">
          <el-date-picker
              v-model="time"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
           </el-date-picker>
        </el-form-item>

        <el-form-item label="一级分类" :label-width="formLabelWidth">
          <el-select v-model="form.first_cateid" placeholder="请选择分类" @change="chagneCate">
            <el-option v-for="item in cateList" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="二级分类" :label-width="formLabelWidth">
          <el-select v-model="form.second_cateid" placeholder="请选择分类" @change="chagneGood">
            <el-option v-for="item in secondCate" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="商品" :label-width="formLabelWidth">
          <el-select v-model="form.goodsid" placeholder="请选择商品">
            <el-option v-for="item in goods" :key="item.id" :label="item.goodsname" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item  label="状态" :label-width="formLabelWidth">
          <el-switch v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1" :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="ok">添 加</el-button>
        <el-button type="primary" v-else @click="update" >修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { successAlert, warningAlert } from '../../util/alert';
import {skillAdd,skillOne,skillUbdate} from "../../util/request"
import {mapGetters,mapActions} from "vuex"
export default {
    props:["info"],
    data(){
        return {
            form:{
              title:"",
              begintime:"",
              endtime:"",
              first_cateid:"",
              second_cateid:"",
              goodsid:"",
              status:1
            },
            formLabelWidth:"120px",
            //存放二级分类
            secondCate:"",
            //存放商品
            goods:"",
            //存放时间
            time:[]
        }
    },
    computed:{
      ...mapGetters({
        goodList:"goods/list",
        cateList:"cate/getCate"
      })
    },
    methods:{
        ...mapActions({
          requestGoods:"goods/goodsActions",
          requestCate:"cate/cateActions",
           requestSkill:"skill/cateActions"
        }),
        cancel(){
            this.info.isShow=false
            this.time=''
            this.form={
               title:"",
              begintime:"",
              endtime:"",
              first_cateid:"",
              second_cateid:"",
              goodsid:"",
              status:1
            };
        },
        ok(){
          this.form.begintime=this.time[0].getTime()
          this.form.endtime=this.time[1].getTime()
          skillAdd(this.form).then(res=>{
            if(res.data.code==200){
              successAlert(res.data.msg)
              this.requestSkill()
              this.cancel()
            }else{
              warningAlert(res.data.msg)
            }
          })
        },
        detail(id){
          skillOne({id:id}).then(res=>{
            this.time.push(new Date(Number(res.data.list.begintime)))
            this.time.push(new Date(Number(res.data.list.endtime)))
             this.form=res.data.list
             this.form.id=id
          })
        },
        update(){
          this.form.begintime=this.time[0].getTime()
          this.form.endtime=this.time[1].getTime()
          skillUbdate(this.form).then(res=>{
            if(res.data.code==200){
              successAlert(res.data.msg)
              this.cancel()
              this.requestSkill()
            }else{
              warningAlert(res.data.msg)
            }
          })
        },
        chagneCate(e){
          this.form.second_cateid=''
          this.secondCate=''
          this.form.goodsid='';
           this.goods='';
          var a=this.cateList.findIndex(item=>item.id==e)
          if(this.cateList[a].children){
            this.secondCate=this.cateList[a].children
          }
        },
        chagneGood(e){
           this.form.goodsid='';
           this.goods='';
          this.goods=this.goodList.filter(item=>item.second_cateid==e)
        }
    },
    mounted(){
      this.requestGoods();
      this.requestCate()
    }
}
</script>

<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .el-icon-plus{
      border: 1px dashed #d9d9d9;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>


